.accor {
    padding: 10px;
    width: 70%;
    border: 1px solid #ddd;
    border-radius: 10px;
}

.accor p {
    padding: 10px;
    letter-spacing: 0.7px;

}

.title {
    display: flex;
    padding: 10px;
    justify-content: space-between;
    align-items: center;
}

.title.border {
    border-bottom: 1px solid #ddd;
}

.title span {
    font-size: 20px;
    cursor: pointer;
}
.title h2{
    font-size: 22px;
}

@media screen and (max-width:768px) {
    .title h2 {
        font-size: 20px;
    }
    .accor p{
        font-size: 13px;
        line-height: 17px;
    }
    
}