@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
  list-style: none;
}

body {
  min-width: 340px;
  font-family: 'Roboto', sans-serif;
}

button {
  cursor: pointer;
}

span,
a,
button,
input,
i,
img {
  display: inline-block;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1300px;
  width: 100%;
  margin: 0 auto;
  padding: 0px 10px;
}

header{
  padding: 25px 0;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
header button{
  display: flex;
  align-items: center;
  gap: 15px;
  background-color: transparent;
  border: none;
  font-size: 25px;
}
.table{
  width: 100%;
}